import React, { useEffect, useRef } from "react";
import { Box } from "grommet";
import SpeechBubble from "./SpeechBubble";
import ChatTextInput from "./ChatTextInput";

const Component = ({ messages, onSendMessage, roleProfiles }) => {
  const container = useRef(null);

  useEffect(() => {
    container && container.current.scrollTo(0, container.current.scrollHeight);
  }, [messages]);

  return (
    <>
      <div
        ref={container}
        style={{ borderRadius: '4px' ,border: '1px solid gray', overflow: "scroll", height: "calc(100% - 35px)" }}
      >
        <Box>
          <Box>
            {messages &&
              messages.map((message, i) => {
                return <SpeechBubble message={message} />;
              })}
          </Box>
        </Box>
      </div>
      <ChatTextInput 
        placeholder="type to ask question"
        onSubmit={onSendMessage} 
      />
    </>
  );
};

export default Component;
